import React,{useState} from 'react'
import {useHistory} from 'react-router-dom'
import {useSelector,useDispatch} from 'react-redux'
import {currentUserSelector} from '../Store/modules/authStore'
import {addEvent} from '../Store/modules/evntStore'
import { Grid, Typography,Box, TextField, Button} from '@material-ui/core/'
import {useStyles} from '../ConfigMaterial/index'
import DateFnsUtils from '@date-io/date-fns';
import frLocale from "date-fns/locale/fr"
import { MuiPickersUtilsProvider, KeyboardDatePicker,} from '@material-ui/pickers';
import moment from 'moment'
import 'moment/locale/fr';

const Event = () => {

    const classes = useStyles()
    const dispatch = useDispatch()
    const history = useHistory()

    const initialState = {titre:'',organisateur:'',lieu:''}

    const [state,setState] = useState(initialState)

    const user = useSelector(currentUserSelector)

    const [selectedDate, setSelectedDate] = React.useState(new Date());

    
  const handleDateChange = (date) => { setSelectedDate(date);};

    const handleChange= (event) => {

        const {name,value} = event.target

        setState({...state,[name]:value})
    }

    const handleCancel = () => {

        setState(initialState)
    }

    const handleSubmit = () => {

        moment.locale('fr')

        state.dateStr = selectedDate.toString()

        state.date = moment(selectedDate).format('LL')
    
        state.uid = user.uid
        state.username = user.username

        dispatch(addEvent(state))
        history.push('/Dashboard')
    }   

    const isNotValid = state.titre === "" || state.organisateur === "";

    return(

        <Grid container component='div' justify='center'>
            <Grid item md={6}>
                <Box mt={5}>
                    <Typography color='secondary' variant='h3'>Cr√©er un √©venement</Typography>
                </Box>
                <div className={classes.underLine}></div>
                <Box mt={4} >
                    <TextField 
                    onChange={handleChange}
                     name="titre"
                     value={state.titre}
                     className={classes.textField}
                     required
                     color='secondary'
                     margin="dense" 
                     type='text' 
                     label="Titre de l'√©venement" 
                     placeholder="mariage, anniversaire, soir√©e,...">
                    </TextField>

                    <TextField 
                    onChange={handleChange}
                     name="organisateur"
                     value={state.organisateur}
                    className={classes.textField}
                    required
                    color="secondary"
                     margin="dense" 
                     type='text' 
                     label="Organisateur(s)" 
                     placeholder="nom des mari√©s, de l'h√¥te,..."
                     >
                    </TextField>

                    <TextField 
                    onChange={handleChange}
                     name="lieu"
                     value={state.lieu}
                    className={classes.textField}
                    color="secondary"
                     margin="dense" 
                     type='text' 
                     label="Lieu" 
                     >
                    </TextField>
                    <MuiPickersUtilsProvider utils={DateFnsUtils} locale={frLocale} >
                        <Grid container >
                            <KeyboardDatePicker
                            format="dd/MM/yyyy"
                            margin="dense"
                            id="date-picker-inline"
                            label="Choisisez une date"
                            value={selectedDate}
                            onChange={handleDateChange}
                            KeyboardButtonProps={{'aria-label': 'change date',}}
                            />
                        </Grid>
                     </MuiPickersUtilsProvider>   
                    <Box component="div" mt={3} >
                    <Button onClick={handleCancel} color="secondary">Annuler</Button>
                    <Button onClick={handleSubmit} disabled={isNotValid}  color="secondary">Valider</Button>
                    </Box>
                </Box>
            </Grid>
        </Grid>
    )
}


export default Event