import { Grid, Typography,Button,Box} from '@material-ui/core'
import React,{useEffect,useState} from 'react'
import {Link} from 'react-router-dom'
import {useSelector} from 'react-redux'
import {eventsSelector} from '../Store/modules/evntStore'
import {loadingSelector} from '../Store/modules/authStore'
import CardEvent from '../Components/Dashboard/CardEvent'


const Dashboard = () => {

    const events = useSelector(eventsSelector)
    const loading = useSelector(loadingSelector)
    const [displayStyle,setDisplayStyle] = useState('none')
  
 useEffect(()=> {

  const timer = setTimeout(() => {setDisplayStyle('')}, 1000);

  return ()=> clearTimeout(timer)

 },[])
  
    return(
        <div>
            {loading && 
            <div>
                { events.length > 0 ? (
                    <Box display='flex' justifyContent='center'>
                        <Grid item md={10} >
                            <CardEvent/>
                        </Grid>             
                    </Box>
                ) : (   

                    <Box display='flex' justifyContent='center' style={{display:displayStyle}}  >
                        <Box mt={5}>
                            <Typography  className='textColor'>Vous n'avez pas cr√©e d'√©venements pour le moment</Typography>
                            <Link className='text-link' to='/Evenement'><Button color='primary'>Cr√©er un √©venement</Button></Link>
                        </Box>
                    </Box>
                )}
                
                </div>
            }
          
        </div>
    )
}

export default Dashboard