import React,{ useState,useEffect} from 'react'
import {useHistory} from 'react-router-dom'
import {useDispatch,useSelector} from 'react-redux'
import {LogOutUser,UserIsLogedSelector} from '../Store/modules/authStore'
import { Container, } from '@material-ui/core'
import DialogConfirm from '../Components/DialogConfirm'

const Signout = () => {

    const dispatch = useDispatch()
    const history = useHistory()
    const userIsLog = useSelector(UserIsLogedSelector)

    const [open,setOpen] = useState(true)

    const dialog = "Confirmez la deconnexion ?"

    useEffect(() => {

        if(!userIsLog){history.push('/Signin')}
    })

   
      const handleLogOut= () => {  

        dispatch(LogOutUser())
        setOpen(false)
        
      }

    return(

        <Container>
            <div className='flex' style={{height:'80vh'}}>
               {open && <DialogConfirm dialog={dialog}  confirm={handleLogOut} /> } 
            </div>
        </Container>
    )
}

export default Signout