import React from 'react'
import {Route,Switch,useRouteMatch} from 'react-router-dom'
import {Grid,Box} from '@material-ui/core/'
import DashNavMenu from '../../Components/Dashboard/DashNavMenu'
import BudgetId from './BudgetId'
import EventId from './EventId'
import PrestatairesId from './PrestatiresId'
import PlanId from './PlanId'
import InvitId from './InvitId'


const MenuEvent = () => {

    let match = useRouteMatch()

  
    return(
        <Box  display="flex">
            <Grid item md={2} >
                <DashNavMenu/>
            </Grid>
            <Grid item md={10}>
                <Switch>
                    <Route exact path={`${match.path}/BudgetId`} component={BudgetId}/>
                    <Route path={`${match.path}/EventId`} component={EventId}/>
                    <Route path={`${match.path}/PrestatairesId`} component={PrestatairesId}/>
                    <Route path={`${match.path}/PlanId`} component={PlanId}/>
                    <Route path={`${match.path}/InvitId`} component={InvitId}/>
                </Switch>
            </Grid>
           
        </Box> 
    )
}

export default MenuEvent