import {createSlice} from '@reduxjs/toolkit'
import * as firebase from '../../Firebase/fbase' 
import {setLoading,setError} from './authStore'
import {createBudget} from './budgetStore'


const initialState = { events : [] , currentEvent : {}}

export const slice = createSlice({

    name:'Events',

    initialState: initialState,

    reducers:{

        setEvents:(state,{payload}) => {state.events = payload},

        setDeleteEvent:(state,{payload})=>{state.events = state.events.filter(item => item.eventId !== payload.eventId )},

        setUpdateEvent:(state,{payload}) => {state.events = state.events.map(item => item.eventId === payload.eventId ? payload : item)},

        setCurrentEvent:(state,{payload}) => {state.currentEvent = payload},
    }
});

export const addEvent = (payload) => async dispatch => {

    dispatch(setLoading(true))

    try {

        const event = await firebase.db.collection('events').add({

            titre: payload.titre, 
            organisateur:payload.organisateur,
            lieu:payload.lieu,
            date:payload.date,
            dateStr : payload.dateStr,
            username: payload.username,
            uid:payload.uid,
            image:null,
            notes:'',
            invit√©Nb:'',
            budgetPre:''
        });

        await firebase.db.collection('events').doc(event.id).update({eventId:event.id})

        dispatch(fetchEvents(payload.uid))
        dispatch(createBudget(event.id))
        dispatch(setLoading(false))

    } catch (error) {console.log(error)}
}

export const fetchEvents = (payload) => async dispatch => {

    dispatch(setLoading(true))

    try {

        const events = await firebase.db.collection('events').where("uid", "==", payload).get()

        if(events.empty){dispatch(setLoading(false)); console.log('no events');

        }  else {

            const eventsArray = [];

            events.forEach(doc => {

            let data = doc.data()
            eventsArray.push(data)
            })
            
            dispatch(setEvents(eventsArray))
            dispatch(setLoading(false))
        }

    } catch (error) {console.log(error)}
}

export const deleteEvent = (payload) => async dispatch => {

    dispatch(setLoading(true))
  
    try {

        await firebase.db.collection('events').doc(payload.eventId).delete()
        dispatch(setCurrentEvent({}))
        dispatch(setDeleteEvent(payload))
        dispatch(fetchEvents(payload.uid))
        dispatch(setLoading(false))
        
    } catch (error) {console.log(error)}
}

export const updateEvent = (payload,file) => async dispatch => {

    dispatch(setLoading(true))
    
    try {

        await firebase.db.collection('events').doc(payload.eventId).update(payload)

        if(payload.image !== null && payload.image !== file){

            await firebase.storage.ref('events/' + payload.eventId).put(file)

            const url = await firebase.storage.ref('events/' + payload.eventId).getDownloadURL()

            await firebase.db.collection('events').doc(payload.eventId).update({image:url})
        }

        dispatch(setUpdateEvent(payload))
        dispatch(fetchEvents(payload.uid))
        
    } catch (error) {dispatch(setError(error.message))}
}

export const {setEvents,setCurrentEvent,setDeleteEvent,setUpdateEvent} = slice.actions

export default slice.reducer



export const eventsSelector = (state) => state.eventStore.events
export const currentEventSelector = (state) => state.eventStore.currentEvent

