import React,{useState} from 'react'
import {useDispatch} from 'react-redux'
import {createUserAccount} from '../Store/modules/authStore'
import { Typography, TextField,Box,Button} from '@material-ui/core'
import {useStyles} from '../ConfigMaterial/index'
import {useHistory} from 'react-router-dom'


const SignUpForm = () => {

    const dispatch = useDispatch()
    let history = useHistory()

    const [user,setUser] = useState({username:'',email:'',password:''})

    const handleChangeInput = (event) => {

      const {name,value} = event.target

      setUser({...user,[name]:value})

    }

    const handleSubmit = (event) => {

        event.preventDefault()
        dispatch(createUserAccount(user))
        history.push('/Dashboard')
    }

    const isNotValid = user.username === '' || user.email === '' || user.password.length < 5

    const classes = useStyles()

    return(
        
        <form>
            <Box className={classes.flexCol} p={3}>
                <Box  mb={1}>
                    <Typography color='primary' variant='h4'>Cr√©er un compte</Typography>
                </Box>
                <TextField onChange={handleChangeInput} value={user.username} name='username' margin='dense' type="text" label="Nom d'utilisateur"></TextField>
                <TextField onChange={handleChangeInput} value={user.email} name="email" margin="dense"  type="email" label="Email" ></TextField>
                <TextField onChange={handleChangeInput} value={user.password} placeholder='au moins 6 charact√®res' name="password" margin='dense' label='Mot de passe' type="password"></TextField> 
            </Box>  
            <Box textAlign='center' p={1} >
                <Button  disabled={isNotValid} onClick={handleSubmit} type='submit' variant='contained' color='secondary'>Valider</Button>
            </Box>   
        </form>
    )
};

export default SignUpForm