import React,{useState} from 'react';
import {useSelector,useDispatch} from 'react-redux'
import {useHistory} from 'react-router-dom'
import {currentEventSelector} from '../../Store/modules/evntStore'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import {Grid,Box, Typography,InputAdornment,Input,InputLabel} from "@material-ui/core/"
import DateFnsUtils from '@date-io/date-fns';
import frLocale from "date-fns/locale/fr"
import { MuiPickersUtilsProvider, KeyboardDatePicker,} from '@material-ui/pickers';
import Icon from '@material-ui/core/Icon';
import 'moment/locale/fr';
import moment from 'moment'
import {useStyles} from '../../ConfigMaterial/index'
import {updateEvent} from '../../Store/modules/evntStore'


const FormEventEdit = (props) => {

  const classes = useStyles()
  const dispatch = useDispatch()
  const history = useHistory()

  const currentEvent = useSelector(currentEventSelector)
  const [editCurrentEvent,setEditCurrentEvent] = useState(currentEvent)
  const [imageUrl,setImageUrl] = useState(editCurrentEvent.image)
  

  const handleClose = () => {props.close()};

  const handleChangeInput = (event) => {

    const {name,value} = event.target
   
    if(name === "image"){

      let file = event.target.files[0]

      let reader = new FileReader()

      reader.readAsDataURL(file)

      reader.addEventListener('load', () => {

        let url = reader.result

        setEditCurrentEvent({...editCurrentEvent,image:url})
        setImageUrl(file)
      })

    } else {

      setEditCurrentEvent({...editCurrentEvent,[name]:value})
    }
  }

  const handleChangeDate = (editDate) => {

    moment.locale('fr')

    let dateObj = moment(editDate).format('LL')

    let dateString =  editDate.toString()
   
   setEditCurrentEvent({...editCurrentEvent,dateStr:dateString,date:dateObj})

  } 
  
  const handleCancel = () => { 
    
    setEditCurrentEvent(currentEvent);
    setImageUrl(currentEvent.image)
    handleClose()
  }

  const handleSubmit = () => {

    setEditCurrentEvent(editCurrentEvent)
    dispatch(updateEvent(editCurrentEvent,imageUrl))
    history.push('/Dashboard')
  }

  
  return (
    <div>
      <Dialog open={true} onClose={handleClose} aria-labelledby="form-dialog-title" scroll="body" disableBackdropClick disableEscapeKeyDown >
        <Box mt={2} ml={3}>
          <Typography color='secondary' variant="h5">Editer votre √©venement</Typography>
        </Box>
        <DialogContent>
          <TextField
            onChange={handleChangeInput}
            name="titre"
            margin="normal"
            label="Titre"
            type="text"
            fullWidth
            value={editCurrentEvent.titre}
          />
           <TextField
            onChange={handleChangeInput}
            name="lieu"
            margin="normal"
            label="Lieu"
            type="text"
            fullWidth
            value={editCurrentEvent.lieu}
          />
           <TextField
            onChange={handleChangeInput}
            name="organisateur"
            margin="normal"
            label="Organisateur"
            type="text"
            fullWidth
            value={editCurrentEvent.organisateur}
          />
          <Box mt={1}>
            <TextField
                onChange={handleChangeInput}
                name="notes"
                id="outlined-full-width"
                label="Commentaires"
                style={{ margin: 8 }}
                placeholder="notes"
                fullWidth
                margin="dense"
                value={editCurrentEvent.notes}
                InputLabelProps={{
            shrink: true,
          }}
          variant="outlined"
        />
        </Box>
        <Box mt={1}>
          <TextField
            onChange={handleChangeInput}
            value={editCurrentEvent.invit√©Nb}
            name="invit√©Nb"
            id="standard-number"
            margin="dense"
            label="Nombre d'invit√©s pr√©vu"
            type="number"
            InputLabelProps={{
            shrink: true,
          }}
          />
          </Box>
          <Box mt={1}>
          <InputLabel htmlFor="standard-adornment-amount">Budget pr√©visionel</InputLabel>
          <Input
            id="standard-adornment-amount"
            name="budgetPre"
            value={editCurrentEvent.budgetPre}
            onChange={handleChangeInput}
            startAdornment={<InputAdornment position="start">euros</InputAdornment>}
          />
          </Box>
          <Box mt={1} >
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={frLocale} >
                    <Grid container >
                        <KeyboardDatePicker
                        onChange={handleChangeDate}
                        name="date"
                        format="dd/MM/yyyy"
                        margin="dense"
                        id="date-picker-inline"
                        label="Choisisez une date"
                        KeyboardButtonProps={{'aria-label': 'change date',}}
                        value={editCurrentEvent.dateStr}
                        />
                    </Grid>
                </MuiPickersUtilsProvider> 
            </Box>
            <Box mt={1} display="flex">
            <Button component='label' label="test" color="secondary" startIcon={<Icon>insert_photo</Icon>}>
              <input onChange={handleChangeInput} name='image' style={{display:'none'}} type='file' accept="image/png, image/jpeg"/>T√©l√©charger une image
            </Button>
            <Box ml={2}>
            { editCurrentEvent.image !== null  ? <img src={editCurrentEvent.image} alt="llla" className={classes.imgPre}/> : null}
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancel} color="primary">
            Annuler
          </Button>
          <Button onClick={handleSubmit} color="primary">
            Valider
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default FormEventEdit
