import React,{useEffect} from 'react'
import {BrowserRouter as Router,Redirect} from 'react-router-dom'
import {useDispatch,useSelector} from 'react-redux'
import {auth} from './Firebase/fbase'
import {Box} from '@material-ui/core/'
import {logInUser,setLoading,UserIsLogedSelector} from './Store/modules/authStore'
import {fetchEvents} from './Store/modules/evntStore'
import TopHeader from './Components/TopHeader'
import {Switch , Route} from 'react-router-dom'
import Dashboard from './Pages/Dashboard'
import Home from './Pages/Home'
import Signup from './Pages/Signup'
import Signin from './Pages/Signin'
import Signout from './Pages/Signout'
import Event from './Pages/Evenement'
import Profile from './Pages/Profile'
import MenuEvent from './Pages/EvenementId/MenuEvent'

const  App = () =>{

  const dispatch = useDispatch()
  const userLog = useSelector(UserIsLogedSelector)

  useEffect(()=>{

    auth.onAuthStateChanged(user => {

      if(user){ 

        dispatch(logInUser(user.uid))
     
        dispatch(fetchEvents(user.uid))
      
      } else { dispatch(setLoading(false))}
    })

  },[dispatch]);

    return(
  <Box className='maxWitdh bG'  style={{height:"100vh"}}>
    <Router>
      <TopHeader/>
        <Switch>
              <Route exact path={'/'} component={Home}/>
              <Route path='/Signin' component={Signin}/>
              <Route path={'/Signup'} component={Signup}/>
              <Route path={'/Dashboard'} component={Dashboard}/>
              <Route path={'/Signout'} component={Signout}/>
              <Route path={'/Profile'} component={Profile}/>
              <Route path={'/Evenement'} component={Event}/>
              <Route path='/MenuEvent/:id' component={MenuEvent}/>
          </Switch>  
         { userLog ? (<Redirect push to='/' />) : (<Redirect push to="/"/>)}
    </Router>
  </Box>
)}
  
export default App;
