import {createSlice} from '@reduxjs/toolkit'
import * as firebase from '../../Firebase/fbase' 

const initialState = { currentUser:{}, loading:false, error:null, UserIsLoged:false};

const slice = createSlice({

    name:'Users',

   initialState: initialState,

   reducers:{

    setCurrentUser:(state,{payload}) => {state.currentUser = payload},

    setUserIsLoged:(state,{payload}) => {state.UserIsLoged = payload},

    setLoading:(state,payload) => {state.loading = payload},

    setError:(state,{payload}) => {state.error = payload}

   }
});

export const {setCurrentUser,setLoading,setError,setUserIsLoged} = slice.actions

export default slice.reducer


export const createUserAccount = (payload) => async dispatch => {

    const email = payload.email
    const password = payload.password

    try {

      const {user} =  await firebase.auth.createUserWithEmailAndPassword(email,password)

      await firebase.db.collection('users').doc(user.uid).set({username:payload.username,email: payload.email,uid:user.uid, avatar:null, password:payload.password})
      
      dispatch(logInUser(user.uid))

    } catch (error) { dispatch(setError(error.message))}

}

export const signInUser = (payload) => async dispatch =>{

    const email = payload.email
    const password = payload.password

    try {

        const {user} = await firebase.auth.signInWithEmailAndPassword(email,password)

        dispatch(logInUser(user.uid))

    } catch (error) {dispatch(setError(error.message))}
}

export const signUpWhithFbook = () => async dispatch =>{

    try {

        const result = await firebase.auth.signInWithPopup(firebase.provider)

        const user = await result.user

        await firebase.db.collection('users').doc(user.$.currentUser.uid).set({
        username:user.$.currentUser.displayName,email:user.$.currentUser.email, signWithFbook:true, uid:user.$.currentUser.uid,avatar:user.$.currentUser.photoURL})
        
        dispatch(logInUser(user.$.currentUser.uid))

    } catch (error) {dispatch(setError(error.message))}

}

export const logWithFbook = () => async dispatch => {

    dispatch(setLoading(true))

    try {

        const result = await firebase.auth.signInWithPopup(firebase.provider)

        const user = await result.user

        dispatch(logInUser(user.$.currentUser.uid))
        dispatch(setLoading(false))
        
    } catch (error) {dispatch(setError(error.message))}
}

export const logInUser = (payload) => async dispatch => {

    dispatch(setLoading(true))
    
    try {

        const user = await firebase.db.collection('users').doc(payload).get()

        dispatch(setUserIsLoged(true))
        dispatch(setCurrentUser(user.data()))
        dispatch(setLoading(false))
       
    } catch (error) { dispatch(setError(error.message))}
}

export const LogOutUser = () => async dispatch => {

    dispatch(setLoading(true))

    try {

        await firebase.auth.signOut()

        dispatch(setUserIsLoged(false))
        dispatch(setCurrentUser({}))
        dispatch(setLoading(false))
        
    } catch (error) {console.log(error)}
}

export const updateUserName = (payload) => async dispatch => {

    dispatch(setLoading(true))

    try {

        await firebase.db.collection('users').doc(payload.uid).update({username:payload.username})

        dispatch(logInUser(payload.uid))
        dispatch(setLoading(false))
        
    } catch (error) {console.log(error)}

}

export const updateEmail = (payload) => async dispatch => {

    dispatch(setLoading(true))

    try {

      await firebase.db.collection('users').doc(payload.uid).update({email:payload.email})

      dispatch(logInUser(payload.uid))
      dispatch(setLoading(true))
        
    } catch (error) { console.log(error) }
}

export const updateEmailProvider = (payload) => async dispatch => {

    dispatch(setLoading(true))

    try {

        const user = await firebase.auth.currentUser

        const cred = await firebase.emailProvider.credential(user.$.currentUser.email,payload.password)

        await user.reauthenticateWithCredential(cred)

        console.log(payload.email)

        await firebase.db.collection('users').doc(user.$.currentUser.uid).update({email:payload.email})
      
        dispatch(logInUser(user.$.currentUser.uid))
        dispatch(setLoading(false))
        
    } catch (error) {console.log(error) }
}

export const addAvatar = (payload,file) => async dispatch => {

    dispatch(setLoading(true))

    try {

        await firebase.storage.ref('avatar/' + payload.uid).put(file)

        const url = await firebase.storage.ref('avatar/'+ payload.uid).getDownloadURL()

        await firebase.db.collection('users').doc(payload.uid).update({avatar:url})

        dispatch(logInUser(payload.uid))
        dispatch(setLoading(false))
        
    } catch (error) {console.log(error)}
}

export const currentUserSelector = (state) => state.authStore.currentUser
export const loadingSelector = (state) => state.authStore.loading
export const errorSelector = (state) => state.authStore.error
export const UserIsLogedSelector = (state) => state.authStore.UserIsLoged