import React from 'react'
import {useSelector} from 'react-redux'
import {errorSelector} from '../Store/modules/authStore'
import SignUpForm from '../Components/SignUpForm'
import FbookLog from '../Components/FbookLog'
import { Grid,Container,Divider,Box} from '@material-ui/core'
import AlertError from '../Components/AlertError'


const Signup = () => {

    const error = useSelector(errorSelector)

    const fromSignup = true
    

    return(

        <Container component='main'  >
            <Grid container  justify='space-around' alignItems="center" style={{height:'70vh'}}>
                <Grid item md={5} >
                    <SignUpForm/> 
                    <Divider/>
                    <Box mt={2}>
                    <FbookLog fromSignup={fromSignup} />
                    </Box>
                    <Divider/>
                    <Box>
                        <AlertError error={error}/>
                    </Box>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Signup