
import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import {Typography,Box,IconButton,Tooltip} from '@material-ui/core/';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Icon from '@material-ui/core/Icon';

const useStyles = makeStyles({
  root: {width: '100%',},
  container: {maxHeight: 430,},
  rotate :{transform: 'rotate(90deg)'},
  noRotate :{transform:'rotate(0deg)'}

});


const ListHosts =  (props) => {

  const classes = useStyles();

  const [transform,setTransform] = useState(false)

  const handleOrder = () => {
    
    !transform ? setTransform(true) : setTransform(false)
     
    props.handleSortByName(transform)
  }

 

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table" onDrop={(event) => props.handleDropBack(event)} onDragOver={(e) => props.handleDragOver(e)} >
          <TableHead>
            <TableRow>
                <TableCell>
                  <Box display="flex" alignItems="center" >
                  <Typography color="primary" variant="h6">Liste des invit√©s</Typography>
                  <Tooltip title="ordre alphab√©tique">
                    <IconButton onClick={handleOrder} >
                      <Icon color="primary" className={!transform ? classes.rotate : classes.noRotate} >chevron_right</Icon>
                    </IconButton>
                  </Tooltip>
                  </Box>
                  </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {props.hosts.map((item,index) => (

              <TableRow key={index} hover style={{cursor:"pointer"}}>
                <TableCell size="small" align="justify">
                  <Box display="flex" justifyContent="space-between" alignItems="center">
                    <Box id="tabHost" draggable onDragStart={(e)=>props.handleDragStart(e,item)} >
                      <Typography>{item.nom}<span style={{marginLeft:"5px"}}>{item.prenom}</span></Typography>
                    </Box>
                    <Box>
                      <IconButton onClick={(e) => props.handleDeleteHost(item)} color="secondary" size="small"><Icon>delete_outline</Icon></IconButton>
                    </Box>
                  </Box>
                </TableCell>  
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
  );
}

export default ListHosts