import React,{useState,useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import {addHosts,createPlanTables,updateTables,deleteTable, fetchHosts,sortHosts} from '../../Store/modules/hostStore'
import {currentEventSelector} from '../../Store/modules/evntStore'
import {hostsSelector,deleteHost,tablesSelector} from '../../Store/modules/hostStore'
import {Box,Button,Grid,Typography,Card,CardContent,Tooltip,IconButton} from '@material-ui/core/'
import {useStyles} from '../../ConfigMaterial/index'
import Icon from '@material-ui/core/Icon';
import FormInvit from '../../Components/Dashboard/FormInvit'
import ListHosts from '../../Components/Dashboard/ListHost'


const PlanId = () => {

    const classes = useStyles()
    const dispatch = useDispatch()

    const currentEvent = useSelector(currentEventSelector)
    const tableArr = useSelector(tablesSelector)
    const hosts = useSelector(hostsSelector)

    useEffect(()=> {

        dispatch(fetchHosts(currentEvent.eventId))
        
    },[dispatch,currentEvent.eventId])

    let table = {nom:"Table",arrayTable:[],id: Date.now(),eventId:currentEvent.eventId,uid:currentEvent.uid}
    const formInvit = {nom:"nom",prenom:"prenom",titre:"Saisir le nom de l'invit√©"}
    const formTable = {nom:"nom",titre:"Nom de la table"}

    const [tableNb,setTableNb] = useState(tableArr)
    const [currentTab,setCurrentTab] = useState(null)
    const [openFormInvit,setOpenFormInvit] = useState(false)
    const [field,setField] = useState()

    const openForm = (e) => {

        e.currentTarget.id === "invite" ? setField(formInvit) : setField(formTable)
        setOpenFormInvit(true) 
    }

    const handleCloseFormInvit = () => { setOpenFormInvit(false)}
  
    const handleDragStart = (event,item) => {event.dataTransfer.setData("item",JSON.stringify(item));setCurrentTab(event.target.id)}
    
    const handleDragStartBack = (event,tabSelected,item) => { event.dataTransfer.setData("item", JSON.stringify(item));setCurrentTab(tabSelected)}

    const handleDrop = (event,id,item) => {

        event.preventDefault()

       let file =  JSON.parse(event.dataTransfer.getData("item"))
   
        tableNb.forEach(tab => { if(tab.id === id && tab.arrayTable.length < 11){ 

            let sameFile = tab.arrayTable.find(item => item.id === file.id)

            if(sameFile === undefined){

            let array = [...tableNb]
            let copyArr = [...item.arrayTable,file]
            let tabObj = {...tab,arrayTable:copyArr}

            let array1 = array.map(tab => tab.id === id ? tab = tabObj : tab)

            dispatch(updateTables(tabObj))
            setTableNb(array1)
               
            if(sameFile === undefined && currentTab !== "tabHost" ){ handleRetriveHost(file,array1)}
 
            if(currentTab === "tabHost" && sameFile === undefined ){ dispatch(deleteHost(file))} 
        
        } else if(tab.id === id && tab.arrayTable.length === 10) {alert('tab plein')} 
    }}
    )}

    const handleRetriveHost = (file,arrayCopy) => {

        arrayCopy.forEach(tab => {  if(tab.id === currentTab.id){

            let arr = [...tab.arrayTable]
            let arrMoved = arr.filter(item => item.id !== file.id)
            let tabObj = {...tab,arrayTable:arrMoved}

            setTableNb(arrayCopy.map(tab =>  currentTab.id === tab.id ?  tab = tabObj : tab))
            setCurrentTab(null)
            dispatch(updateTables(tabObj))
        }})
    }

    const handleDropBack = (event) => {

        event.preventDefault()

        let file =  JSON.parse(event.dataTransfer.getData("item")) 

        if(currentTab !== "tabHost"){

            handleRetriveHost(file,tableNb)
            dispatch(addHosts(file))
        } 
    }

    const handleDragOver = (e) => {e.preventDefault(); e.dataTransfer.dropEffect = 'move'}

    const handleTableAdd = (tabNom) => { 

        dispatch(createPlanTables(table))
        if(tabNom !== ""){table.nom = tabNom}
        setTableNb([...tableNb,table])
    } 

        
    const handleDeleteTab = (tab) => {

      tableNb.forEach(item => {

        if(tab.id === item.id){

           const backHost = [...tab.arrayTable]

           backHost.forEach(host => {dispatch(addHosts(host))})
           setTableNb(tableNb.filter(tab => tab.id !== item.id))
        }
      })

      dispatch(deleteTable(tab.id))
    }

    const addInvite = (host) => {dispatch(addHosts(host))}

    const handleDeleteHost = (item) => { dispatch(deleteHost(item))}

    const handleSortByName = (ord) => { dispatch(sortHosts(hosts,ord))}

    return( 
        <Grid container direction="column">
            <Grid item md={12} display="flex">
               {openFormInvit && <FormInvit addInvite={addInvite} addTable={handleTableAdd} field={field} Close={handleCloseFormInvit} eventId={currentEvent.eventId} uid={currentEvent.uid} />} 
                <Box mt={1}>
                    <Typography  color="secondary" variant="h4">Plan de table</Typography>
                    <div style={{width:"18%"}} className={classes.underLine}></div>
                </Box>
                <Box>
                    <Box mt={4} display="flex">
                       
                        <Box mr={1}>
                            <Button onClick={(e) => openForm(e)} id="invite" color="primary" startIcon= {<Icon color="primary">supervised_user_circle</Icon>}>Ajouter Invit√©s</Button>
                        </Box>
                        <Box>
                            <Button onClick={(e) => openForm(e)} id="table" color="primary" startIcon= {<Icon color="primary">add_box</Icon>} >Ajouter table</Button>
                        </Box>
                    </Box>
                </Box>
            </Grid>
            <Grid container>
                <Grid item md={3}>
                    <ListHosts hosts={hosts}
                    handleSortByName={handleSortByName}
                    handleDeleteHost={handleDeleteHost} 
                    handleDropBack={handleDropBack} 
                    handleDragOver={handleDragOver} 
                    handleDragStart={handleDragStart}/>
                </Grid>
                    <Grid item md={9}>
                        <Box display="flex" flexWrap="wrap">
                            {tableNb.map((item,index) => (
                                <Box ml={3} mb={2} key={index} onDrop={(event) => handleDrop(event,item.id,item)} onDragOver={(e) => handleDragOver(e)}>
                                    <Card elevation={3} className={classes.cardTab}>
                                        <CardContent>
                                        <Typography color="primary" variant="h6">{item.nom}</Typography>
                                        {  item.arrayTable.map((tab,index) => (  
                                            <Typography variant="body2"  component="p"  style={{cursor:"pointer"}}
                                            key={index} 
                                            draggable 
                                            onDragStart={(e)=>handleDragStartBack(e,item,tab)}>
                                                {tab.nom}<span style={{marginLeft:"3px"}}>{tab.prenom}</span> 
                                            </Typography>      
                                            
                                        ))}
                                        </CardContent>
                                        <Box>
                                        <Tooltip title="Supprimer ?">
                                            <IconButton onClick={() => handleDeleteTab(item)} color="secondary"><Icon>clear</Icon></IconButton>
                                        </Tooltip>
                                        </Box>
                                    </Card>
                                </Box>
                            ))} 
                        </Box>
                    </Grid>
            </Grid>
        </Grid>
    )
    
}

export default PlanId