import React from 'react'
import {useSelector,useDispatch} from 'react-redux'
import {Grid,Typography,Box,Button} from '@material-ui/core/';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableFooter from '@material-ui/core/TableFooter';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon'
import {useStyles} from '../../ConfigMaterial/index'
import {formaMoney} from '../../Utils'
import BudgetEdit from '../../Components/Dashboard/BudgetEdit'
import {currentEventSelector} from '../../Store/modules/evntStore'
import {budgetSelector,fetchBudget} from '../../Store/modules/budgetStore'

const BudgetId = () => {

    const classes = useStyles()
    const dispatch = useDispatch()
    const [edit,setEdit] = React.useState(false)
    const [editCout,setEditCout] = React.useState({})
   
    const currentEvent = useSelector(currentEventSelector)

    React.useEffect(() => {

        dispatch(fetchBudget(currentEvent.eventId))

    },[dispatch,currentEvent.eventId])

    const budget = useSelector(budgetSelector)

    const handleSelectCout = (cout) => {

        setEditCout(cout)
        setEdit(true)
    }

    const handleAddCout = () => {

        let cout = {titre:"Saisir libell√©",nom:"",coutPre:0,coutReel:0,eventId:currentEvent.eventId}
        setEditCout(cout)
        setEdit(true)
    }

    const handleTotalCoutPre = () => {

        let total = 0;

        budget.forEach(item => {  total = total + parseFloat(item.coutPre)})

        return formaMoney(total.toFixed(2))
    }

    const handleTotalCoutReel = () => {

        let total = 0;

        budget.forEach(item => total += parseFloat(item.coutReel))

        return formaMoney(total.toFixed(2))
    }

    const handleCloseEdit = () => setEdit(false)

    return(

        <Grid container>
            <Grid item md={2}>
                <Box>
                    <Typography  color="secondary" variant="h5" >Budget</Typography>
                    <div style={{width:"50%"}} className={classes.underLine}></div>
                </Box>
                <Box mt={5}>
                    <Button onClick={handleAddCout} color="primary" startIcon={<Icon color="primary">add_box</Icon>}>AJOUTER LIBELLE</Button>
                </Box>
            </Grid>
            <Grid item md={4} >
                <Box mt={3}>
                    <TableContainer elevation={3} component={Paper}>
                        <Table >
                            <TableHead className={classes.headCellTable}>
                                <TableRow>
                                    <TableCell style={{color:'#ff5730'}}>Libell√©</TableCell>
                                    <TableCell style={{color:'#ff5730'}}  align="right">Co√ªt Pr√©visionnel</TableCell>
                                    <TableCell style={{color:'#ff5730'}} align="right">Co√ªt R√©el</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {budget.map((item,index) => 

                                    <TableRow onClick={() => handleSelectCout(item)}  key={index}  style={{cursor:"pointer"}}>
                                        <TableCell  size="small" style={{fontSize:"0.9em"}} component="th" scope="row">{item.nom}</TableCell>
                                        <TableCell size="small"  align="center">{formaMoney(item.coutPre)}</TableCell>
                                        <TableCell size="small"  align="center">{formaMoney(item.coutReel)}</TableCell>
                                    </TableRow>
                                )}
                                <TableRow>
                                </TableRow>
                            </TableBody>
                            <TableFooter>
                                <TableRow className={classes.headCellTable}>
                                    <TableCell size="small"><Typography color="primary">Total</Typography> </TableCell>
                                    <TableCell size="small"  align="center"><Typography component="body2" color="secondary">{handleTotalCoutPre()}</Typography></TableCell>
                                    <TableCell size="small"  align="center"><Typography component="body2" color="secondary">{handleTotalCoutReel()}</Typography></TableCell>
                                </TableRow>
                            </TableFooter>
                        </Table>
                    </TableContainer>
                </Box>
            </Grid>
            <Grid item>
                <Box mt={3} ml={4}>
                {edit && <BudgetEdit cout={editCout} close={handleCloseEdit}/>}
                </Box>
            </Grid>
        </Grid>
       
    )

}

export default BudgetId