import React from 'react'
import { CardActions, CardContent,Card,Typography,Box, IconButton,Tooltip} from '@material-ui/core'


import Icon from '@material-ui/core/Icon';

const CardPresta = (props) => {

   
    const handleEditPresta = (presta) => {

        props.handleEdit(presta)
        props.openForm(presta.categorie)
    }

    return(
        <Box mb={1} key={props.index}>
            <Card className="pad" style={{maxWidth:"230px"}} elevation={3}>
                <CardContent>
                    <Box mb={1}>
                        <Typography variant='h5' color='secondary'>{props.item.nom}</Typography>
                    </Box>
                    <Box mb={1}>
                        <Typography color='textPrimary'>{props.item.categorie}</Typography>
                    </Box>
                    <Box mb={1} display="flex">
                      <Box mr={1}>
                          <Typography color="textSecondary" style={{fontSize:"0.9em"}} >Adresse</Typography>
                        </Box>  
                      <Typography style={{fontSize:"0.8em"}} variant="body2" compoonent="p">{props.item.adresse}</Typography>
                    </Box>
                    <Box mb={1} display="flex">
                        <Box mr={1}>
                          <Typography color="textSecondary" style={{fontSize:"0.9em"}} >Phone</Typography>
                        </Box> 
                        <Typography  variant="body2" compoonent="p">{props.item.phone}</Typography>
                    </Box>
                    <Box mb={1} display="flex">
                        <Box mr={1}>
                          <Typography color="textSecondary" style={{fontSize:"0.9em"}} >Mail</Typography>
                        </Box> 
                        <a className="overFloxWrap" style={{fontSize:"0.8em"}} href={`mailto:${props.item.mail}`}>{props.item.mail}</a>
                    </Box>
                    <Box mb={1} display="flex">
                        <Box mr={1}>
                          <Typography color="textSecondary" style={{fontSize:"0.9em"}} >Site</Typography>
                        </Box> 
                        <a className="overFloxWrap" style={{fontSize:"0.8em"}} target="blank" href={props.item.web}>{props.item.web}</a>
                    </Box>  
                 </CardContent>
                <CardActions>
                    <Box display='flex' alignItems='center'  style={{width:"100%"}} >
                        <Box>
                            <Tooltip title="Editer">
                                <IconButton onClick={()=> handleEditPresta(props.item)} ><Icon>edit</Icon></IconButton>
                            </Tooltip>
                        </Box>
                        <Box>
                            <Tooltip title="Supprimer">
                                <IconButton onClick={() => props.handleDelete(props.item)} ><Icon>delete</Icon></IconButton>
                            </Tooltip>
                        </Box> 
                    </Box>
                </CardActions>
            </Card>
        </Box>
    )
}


export default CardPresta