import React,{useState,useEffect} from 'react'
import {useHistory} from 'react-router-dom'
import {useStyles} from '../ConfigMaterial/index'
import {useDispatch,useSelector} from 'react-redux'
import {Grid, Container,TextField,Box,Button,Typography,Divider} from '@material-ui/core'
import { signInUser,errorSelector,setError,UserIsLogedSelector} from '../Store/modules/authStore'
import AlertError from '../Components/AlertError'
import FbookLog from '../Components/FbookLog'


const Signin = () => {

  const classes = useStyles()
  const dispatch = useDispatch()
  const history = useHistory()
  const error = useSelector(errorSelector)
  const userIsLog = useSelector(UserIsLogedSelector)


  useEffect(()=> { 

    if(userIsLog){history.push('/Dashboard')}
  })

  const [user,setUser] = useState({email:'',password:''})

  const handleChangeInput = (event) => {

    const {value,name} = event.target

    setUser({...user,[name]:value})
  }

  const handleSubmitForm = (event) => {

    event.preventDefault()
    dispatch(signInUser(user))

  }

  const clearError = () => {

    if(error !== null){

      dispatch(setError(null))
    }
  }

    return(

      <Container component='main' >
        <Grid container justify="space-around" style={{height:'65vh'}} alignItems='center'> 
          <Grid  item md={5} >
                <form>
                <Box className={classes.flexCol} p={4}>
                    <Box >
                        <Typography color='primary' variant='h4'>Se connecter</Typography>
                    </Box>
                    <TextField onFocus={clearError} onChange={handleChangeInput} value={user.email} name='email' required margin="dense"  type="email" label="Email" ></TextField>
                    <TextField onChange={handleChangeInput} value={user.password} name='password' placeholder="Au moins 6 caract√®res" required margin='dense' label='Mot de passe' type="password"></TextField> 
                </Box>
                <Box p={2}  display='flex'justifyContent='space-between'>
                   <Box><Button onClick={handleSubmitForm} type="submit" variant='contained' color='secondary'>Valider</Button></Box>
                   <Box><Button  color="secondary">Mot de passe oubli√©</Button></Box>
                </Box>
                </form>
            <Divider/>
            <Box component='div' mt={1}>
            <FbookLog/>
            </Box>
            <Divider/>
            <Box mt={2} >
                  <AlertError error={error}/>
            </Box>
          </Grid>
        </Grid>
      </Container>
    )
}

export default Signin