import React,{useEffect} from 'react'
import {useDispatch,useSelector} from 'react-redux'
import {useHistory} from 'react-router-dom'
import {logWithFbook,signUpWhithFbook, UserIsLogedSelector} from '../Store/modules/authStore'
import {IconButton,Typography,Box } from '@material-ui/core'
import Icon from '@material-ui/core/Icon';


const FbookLog = (props) => {

    const dispatch = useDispatch()
    const history = useHistory()

    const userIsLog = useSelector(UserIsLogedSelector)
    
    useEffect(() => {

        if(userIsLog){history.push('/')}
    })

    console.log(props.fromSignup)

    const handleFbookLog = () => {

        props.fromSignup ? dispatch(signUpWhithFbook()) : dispatch(logWithFbook())
    }

    return(

        <Box component='div' display='flex' alignItems='center'>
            <Typography color='primary' variant='h6'> Ou se connecter via Facebook ?</Typography>
            <IconButton  onClick={handleFbookLog}><Icon fontSize='large' style={{color:'#3b5998'}} >facebook</Icon></IconButton> 
        </Box>
    )
}

export default FbookLog