import {configureStore} from '@reduxjs/toolkit'
import {combineReducers} from 'redux'

import authStore from './modules/authStore'
import eventStore from './modules/evntStore'
import hostStore from './modules/hostStore'
import prestaStore from './modules/prestaStore'
import budgetStore from './modules/budgetStore'

const reducer = combineReducers({authStore,eventStore,hostStore,prestaStore,budgetStore})

const store = configureStore({reducer})

export default store