import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Icon from '@material-ui/core/Icon';
import { Typography } from '@material-ui/core';
import {Link} from 'react-router-dom'

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },

  title:{
    
    color:'white'
  },

  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },

  drawerHeader: {
    
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
    background: ' rgb(195,150,90) linear-gradient(0deg, rgba(270,20,50,0.8) 5%, rgba(300,90,96,1) 50%)', 
  },

}));

const DrawerNav = (props)=> {
  const classes = useStyles();
  const theme = useTheme();

  return (
    <div className={classes.root}>
      <CssBaseline />
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={props.open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
        <Typography variant="h6" color='inherit' className={classes.title}>Wedd Easy</Typography>
          <IconButton onClick={props.close} style={{color:'white'}} >
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>  
        <Divider />
        <List>
          {props.navItems.map((item, index) => (
            <ListItem button key={index}>
              <ListItemIcon><Icon>{item.icon}</Icon></ListItemIcon>
             <Link className='text-link' to={item.link}><ListItemText><Typography color='textSecondary'>{item.name}</Typography></ListItemText></Link>
            </ListItem>
          ))}
        </List>
      </Drawer>
        <div className={classes.drawerHeader} />
    </div>
  );
}

export default DrawerNav