import React,{useState} from 'react';
import {useSelector} from 'react-redux'
import {Link} from 'react-router-dom'
import {currentUserSelector,loadingSelector} from '../Store/modules/authStore'
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Navigation from './Navigation'
import Loader from './Loader'
import DrawerNav from './Drawer'
import { Hidden, Container,Box, Avatar } from '@material-ui/core';
import Icon from '@material-ui/core/Icon';
import {themeGlobal} from '../ConfigMaterial/index'


const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({

  root: {
    flexGrow: 1,
  },

  small: {
    width: theme.spacing(4),
    height: theme.spacing(4),
    backgroundColor:themeGlobal.palette.primary.main,
    marginLeft:theme.spacing(2)
  },

  avatar:{

    backgroundColor:themeGlobal.palette.secondary.light
  },

  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },

  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),

    background: ' rgb(195,150,90) linear-gradient(0deg, rgba(270,20,50,0.8) 5%, rgba(300,90,96,1) 50%)', 
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
}));


const TopHeader = () => {

  const [open,setOpen] = useState(false)
  
  const [displayStyle,setDisplayStyle] = useState("none")

  setTimeout(() => {

    setDisplayStyle("")
    
  }, 1000);
  
  const handleOpenDrawer = () => {setOpen(true)}

  const handleCloseDrawer = () => {setOpen(false)}

  let userLog = useSelector(currentUserSelector)
  const loading = useSelector(loadingSelector)

 
  const classes = useStyles();

  let navItems = [];

  const navNoUser = [ {link:'/Signup', name:'Cr√©er un compte', icon: 'person_add'}, {link:'/Signin', name:'Se connecter',icon:'login'}]

  const navUser = [ 

    {link:'/Dashboard', name:'Dashboard', icon: 'dashboard'},
    {link:'/Evenement', name:'Cr√©er votre √©venement', icon: 'movie_filter'},
    {link:'/Profile', name:'Profile', icon: 'person'},
    {link:'/Signout', name:'Deconnexion', icon:'person_outline'},
  ]
  
   if(userLog.username){ 
    navItems = [...navUser]
  } else {

    navItems = [...navNoUser]
  }

  const RenderAvatar = () => {

    if(userLog.username){

      return(

        <IconButton color='inherit'>
            <Typography color='inherit'>{userLog.username}</Typography>{
              userLog.avatar == null ? (<Avatar className={classes.avatar}><Icon>face</Icon></Avatar>) : 
              (<Avatar className={classes.small} src={userLog.avatar} ></Avatar>)
            }
        </IconButton>
      )
    } else {return null}
  }

   

  return (
    <div className={classes.root}>

      {loading ? (  
      <div style={{display:displayStyle}} >
      <AppBar position="static"
       className={clsx(classes.appBar, {
        [classes.appBarShift]: open,
      })}>
        <Toolbar >
          <Box className='maxWidth' component="div" display="flex" alignItems="center" justifyContent="space-between">
            <Box component='div' display='flex' alignItems='center' >
              <IconButton onClick={handleOpenDrawer} edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                <MenuIcon />
              </IconButton>
              <Link className="text-link" to='/'><Typography  variant="h6" className={classes.title}> Wedd Easy</Typography></Link>
            </Box>
          <Hidden xsDown><Navigation navItems={navItems}/></Hidden>
          {open ? <DrawerNav navItems={navItems} close={handleCloseDrawer} open={open} /> : null}
          </Box>
          <RenderAvatar/>
        </Toolbar>
      </AppBar></div>) : (
      <Container>
        <div className='maxHeight flex'> 
          <Loader/>
        </div>
      </Container>
      )
      }
    </div>
  )
}

export default TopHeader

