
import React from 'react'
import {useDispatch} from 'react-redux'
import {Link} from 'react-router-dom'
import {useStyles} from "../../ConfigMaterial/index"
import { CardActions, CardContent,Card,Button,Typography,Box,Grid,CardActionArea} from '@material-ui/core'
import {useSelector} from 'react-redux'
import {eventsSelector,setCurrentEvent} from '../../Store/modules/evntStore'
import {fetchHosts} from '../../Store/modules/hostStore'

const CardEvent  = () => {

    const classes = useStyles()
    const dispatch = useDispatch()
    const events = useSelector(eventsSelector)

    const handleSelect = (id) => {

       let selectedEvnt = events.find(item => item.eventId === id)
       dispatch(setCurrentEvent(selectedEvnt))
       dispatch(fetchHosts(selectedEvnt.eventId))
    }

    return(

        <Grid container display='flex' >
            <Grid container >
                <Grid item  md={4} style={{width:"100%"}}>
                    <Box mt={3}>
                        <Typography color='secondary' variant='h3'>Vos √©venements</Typography>
                    </Box>
                    <Box mt={1} mb={1} className={classes.underLine}></Box>
                </Grid>
            </Grid>
            {events.map((item,index) =>
            <Grid item key={index}  md={3} >
                <Card raised className={classes.cardRoot}>
                  <Link className='text-link' to={`MenuEvent/${item.eventId}`} >
                      <CardActionArea onClick={()=> handleSelect(item.eventId)} >
                        <CardContent>
                            <Box mb={1}>
                                <Typography variant='h5' color='primary'>{item.titre}</Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="h6" color='textSecondary'>Informations</Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Date : <span>{item.date}</span></Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Organisateur : <span>{item.organisateur}</span></Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Lieu : <span>{item.lieu}</span></Typography>
                            </Box>
                        </CardContent>
                        </CardActionArea></Link>
                    <CardActions>
                        <Box display='flex' alignItems='center' justifyContent="space-between" style={{width:"100%"}} >
                            <Link className='text-link'to={`MenuEvent/${item.eventId}`}><Button onClick={()=>handleSelect(item.eventId)} color='primary' size="small">Explorer</Button></Link>
                       </Box>
                    </CardActions>
                </Card>
            </Grid>
            )}
        </Grid>
    )
}


export default CardEvent