import React,{useState} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import { useHistory} from 'react-router-dom'
import {Grid,Card, CardActions,CardContent,Typography,Box,Tooltip, IconButton, CardMedia} from '@material-ui/core/';
import Icon from '@material-ui/core/Icon';
import {currentEventSelector,deleteEvent} from '../../Store/modules/evntStore'
import {errorSelector, setError} from '../../Store/modules/authStore'
import DialogConfirm from "../../Components/DialogConfirm"
import FormEventEdit from "../../Components/Dashboard/FormEventEdit"
import {useStyles} from '../../ConfigMaterial/index'
import {formaMoney} from '../../Utils'
import AlertError from '../../Components/AlertError'

const EventId = () => {

    const currentEvnt = useSelector(currentEventSelector)
    const error = useSelector(errorSelector)
    const dispatch = useDispatch()
    const history = useHistory()
    const classes = useStyles()


    const [open,setOpen] = useState(false)
    const [openEdit, setOpenEdit] = useState(false)

    const dialog = "Confirmez la suppression de l'√©venement ?"

    const clearError = () => { if(error !== null){dispatch(setError(null))} }

    const handleCloseEditForm = () => setOpenEdit(false)

    const handleOpenEdit = () => {setOpenEdit(true); clearError();}

    const handleDeleteDialog = () => setOpen(true)

    const handleDelete = () => {

        setOpen(false)
        dispatch(deleteEvent(currentEvnt))
        history.push('/Dashboard')
        clearError()
    }

    const RenderImage = () => {

        if(currentEvnt.image === null || undefined){

            return (null) 

        } else { return (<CardMedia className={classes.media} image={currentEvnt.image}></CardMedia>)}
    }

   
    return(
        <Grid container>
            <Grid item md={3}>
                <Box>
                    <Typography  color="secondary" variant="h5" >G√©rer votre √©venement</Typography>
                    <div className={classes.underLine}></div>
                </Box>
                <Box mt={6} p={2}>
                    <AlertError error={error}/>
                </Box>
            </Grid>
            <Grid item md={3}>
                <Box mt={6}> 
                   {open && <DialogConfirm dialog={dialog} confirm={handleDelete} /> } 
                   {openEdit && <FormEventEdit close={handleCloseEditForm}/>}
                    <Card raised>
                       {RenderImage()}
                        <Box textAlign="center">
                        <CardContent>
                            <Box my={1}>
                                <Typography color='primary' variant="h6" component="h1">{currentEvnt.titre}</Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Organisateur : <span className={classes.bold} >{currentEvnt.organisateur}</span></Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Date : <span className={classes.bold} >{currentEvnt.date}</span></Typography>
                            </Box>
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Lieu : <span className={classes.bold} >{currentEvnt.lieu}</span></Typography>
                            </Box>
                            { currentEvnt.notes !== "" && 
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Notes : <span className={classes.bold} >{currentEvnt.notes}</span></Typography>
                            </Box>}
                            { currentEvnt.invit√©Nb !== "" && 
                            <Box my={1}>
                                <Typography  variant="body2" component="p">Nb d'invit√©s pr√©vus : <span className={classes.bold}  >{currentEvnt.invit√©Nb}</span></Typography>
                            </Box>}
                            { currentEvnt.budgetPre !== "" && 
                            <Box my={1}>
                                <Typography variant="body2" component="p">Budget pr√©visionel : <span className={classes.bold}>{formaMoney(currentEvnt.budgetPre)} </span></Typography>
                            </Box>}
                        </CardContent>
                        </Box>
                        <CardActions>
                            <Box style={{width:"100%"}} display="flex">
                                <Box >
                                    <Tooltip title="Supprimer l'√©venement" arrow>
                                    <IconButton onClick={handleDeleteDialog} ><Icon  color="primary">delete_forever</Icon></IconButton>
                                    </Tooltip>
                                </Box>
                                <Box >
                                    <Tooltip title="Editer" arrow>
                                    <IconButton onClick={handleOpenEdit} ><Icon color="primary">create</Icon></IconButton>
                                    </Tooltip>
                                </Box>
                            </Box>
                        </CardActions>
                    </Card>
                </Box>
            </Grid>
        </Grid>
    )
}


export default EventId