import {createMuiTheme} from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';

 export const themeGlobal = createMuiTheme({

    palette:{
        primary :{

            main:'#ff5730',
            contrastText:'#fff'
        },

        secondary : {

            main:'#ff5252',
            contrastText:'#fff'
        },
    },

})


export const useStyles = makeStyles((theme) => ({

    headCellTable: {

        backgroundColor:'rgba(250, 250,170, 0.9) !important'
    },

 
    flexCol: {

        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        flexWrap:'wrap'
    },

    cardRoot:{

        width:"250px",
        minHeight:"250px",
        marginTop:'20px',
        backgroundColor:'rgba(250, 240, 250, 0.9) !important'
    },

    cardTab:{

        minWidth:"130px",
        minHeight:"100px",
        backgroundColor:'rgba(250, 240, 250, 0.9) !important'
    },

    bold:{

        fontWeight:"500"
    },

    media: {
        height: 0,
        paddingTop: '56.25%', 
    },

    white:{
        color:'white'
    },

    imgPre:{

        height: "55px",
        width: "65px",
    },

    underLine:{

        borderBottom:'2px solid #ff5252',
        width:"95%"
    },

    roseBg :{

        backgroundColor:'#ff5252'
    },

    bgCard:{

        backgroundColor:' rgba(210, 150, 186, 0.12)'
    },

    textField: {

        padding:'20px 0px',
        width:"85%",
    },

  
    success:{

        color:'#4caf50'
    },

   echec:{

    color:'#f44336'
   }

}));

    


