import {createSlice} from '@reduxjs/toolkit'
import * as firebase from '../../Firebase/fbase' 

const initialState = { prestas : []}

export const slice = createSlice({

    name: "Prestas",

    initialState: initialState,

    reducers: {

        setPresta:(state,{payload}) => {state.prestas = payload},

        setAddPresta:(state,{payload}) => {state.prestas = [...state.prestas,payload]},

        setDeletePresta:(state,{payload}) => {state.prestas = state.prestas.filter(item => item.id !== payload.id)}
    }

})

export const AddPresta = (payload,eventId) => async dispatch => {

    try {

        const presta = await firebase.db.collection('prestas').add({nom:payload.nom,categorie:payload.categorie,adresse:payload.adresse,web:payload.web,phone:payload.phone,mail:payload.mail,eventId:eventId})
        
        await firebase.db.collection('prestas').doc(presta.id).update({id:presta.id})

        dispatch(fetchPresta(eventId))

    } catch (error) {console.log(error) }
    
}

export const fetchPresta = (payload) => async dispatch => {

    try {

        const prestas = await firebase.db.collection('prestas').where('eventId','==',payload).get()

        if(prestas.empty){

            console.log("no prestats")

        } else {

            const prestArr = []

            prestas.forEach(presta => {prestArr.push(presta.data())})

            dispatch(setPresta(prestArr))
        }
        
    } catch (error) {console.log(error)}
}

export const deletePresta = (payload) => async dispatch => {

    try {

        await firebase.db.collection("prestas").doc(payload.id).delete()
        dispatch(setDeletePresta(payload))
        
    } catch (error) {console.log(error) }

}

export const updatePresta = (payload,eventId) => async dispatch => {

    try {

        await firebase.db.collection('prestas').doc(payload.id).update({nom:payload.nom,adresse:payload.adresse,phone:payload.phone,web:payload.web})
        dispatch(fetchPresta(eventId))
        
    } catch (error) {console.log(error)}
}



export const {setAddPresta,setPresta,setDeletePresta} = slice.actions

export default slice.reducer

export const prestasSelector = (state) => state.prestaStore.prestas
