import React,{useState} from 'react'
import {useSelector} from 'react-redux'
import {currentUserSelector} from '../Store/modules/authStore'
import {CardContent, Grid,Typography, CardActions,Box, Button,Avatar} from '@material-ui/core/'
import Icon from '@material-ui/core/Icon';
import {useStyles} from '../ConfigMaterial/index'
import ProfilEdit from '../Components/ProfilEdit'

const Profil = () => {

    const [open,setOpen] = useState(false)

    const user = useSelector(currentUserSelector)
    const classes = useStyles()

    const RenderAvatar = () => {

        if(user.avatar !== null){

            return(<Avatar src={user.avatar}></Avatar>) 

        } else { 
            
            return(<Avatar className={classes.avatar}><Icon>face</Icon></Avatar>)
        }
    }

    const handleToggleOpen = () => { setOpen(true)}

    const handleToggleClose = () => {setOpen(false)}

    return(

        <Grid container justify='center'>
            {open && <ProfilEdit open={open} close={handleToggleClose} />}
            <Grid component='div' item md={4} style={{marginTop:'50px'}}>  
                    <Box  p={3} className={classes.bgCard}>
                        <Typography color='primary' variant='h4'>Profile</Typography>
                        <CardContent>
                            <Typography className={classes.textField}>Nom d'utilisateur : <span style={{marginLeft:'5px'}}>{user.username}</span></Typography>
                            <Typography className={classes.textField}>Email : <span style={{marginLeft:'5px'}}>{user.email}</span></Typography>
                            <Box component='div' display='flex'>
                                <Typography className={classes.textField} >Avatar :</Typography><span style={{marginLeft:'20px'}}><RenderAvatar/></span>
                            </Box>
                        </CardContent>
                        <CardActions>
                            <Button onClick={handleToggleOpen} variant='contained' color='secondary' endIcon={<Icon >create</Icon>}>Editer</Button>
                        </CardActions> 
                    </Box>
            </Grid>
        </Grid>

    )
}

export default Profil