import React,{useState} from 'react';
import {useSelector,useDispatch} from  'react-redux'
import {currentUserSelector,updateUserName,updateEmail,updateEmailProvider,addAvatar} from '../Store/modules/authStore'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import {Typography,TextField,Box, Avatar} from '@material-ui/core/'
import Icon from '@material-ui/core/Icon';
import DialogConfirm from './DialogConfirm'

const ProfilEdit = (props) => {

  const user = useSelector(currentUserSelector)
  const dispatch = useDispatch()

  const [userEdited,setUserEdited] = useState(user)
  const [open,setOpen] = useState(false)
  const [name,setName] = useState(null)
  const [fileAvatar,setFileAvatar] = useState({})


  const handleChangeInput = (event) => {

    const {value,name} = event.target

    if(name === "avatar"){

      let file = event.target.files[0]
      let reader = new FileReader()

      setFileAvatar(file)

      reader.readAsDataURL(file)

      reader.addEventListener('load', () => { 
        
        let url = reader.result
    
        setUserEdited({...userEdited,avatar:url})
        setName(name)
        setOpen(true)
        
      })

    } else { setUserEdited({...user,[name]:value})}
  }

  const handleSubmit = () => {

    
    if(name === "username"){dispatch(updateUserName(userEdited));props.close() }

    if(name === "email" && user.signWithFbook){ dispatch(updateEmail(userEdited));props.close()}

    if(name ==="email" && !user.signWithFbook){ dispatch(updateEmailProvider(userEdited));props.close()}

    if(name === "avatar"){ dispatch(addAvatar(userEdited,fileAvatar)); props.close()}

    setOpen(false)
    
  }

  const handleCancel = () => {

    setUserEdited(user)
    setOpen(false)
   
  }

  const openConfirmEdit = (event) => {

    let {name} = event.target
    if(userEdited.username !== user.username || userEdited.email !== user.email || userEdited.avatar !== user.avatar){

      setName(name)
      setOpen(true)

    }
  }

  const dialog = "confirmez le changement d' " + name;

  const RenderAvatar = () => {

    if(userEdited.avatar == null){

      return(<Avatar><Icon>face</Icon></Avatar>)

    } else {

      return(<Avatar src={userEdited.avatar}></Avatar>)
    }
  }


  return (
    <div>
      {open && <DialogConfirm dialog={dialog}  from={"ProfilEdit"} cancel={handleCancel} confirm={handleSubmit} /> }
      <Dialog
        open={props.open}
        onClose={props.close}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        maxWidth='sm'
      >
       <Box p={2}>
         <Typography variant='h5'color='primary'>Editer votre Profil</Typography>
        </Box>
        <DialogContent>
        <TextField
            onBlur={openConfirmEdit}
            onChange={handleChangeInput}
            margin="dense"
            name="username"
            label="Nom d'utilisateur"
            type="text"
            fullWidth
            color='primary'
            value={userEdited.username} 
          />
         
          <TextField
            onChange={handleChangeInput}
            onBlur={openConfirmEdit}
            margin="dense"
            name='email'
            label="Email"
            type="email"
            color='primary'
            fullWidth
            value={userEdited.email}
          />
          
          <Box mt={5} display='flex' >
            <Button component='label' color="secondary">
              <input name='avatar'  onChange={handleChangeInput} style={{display:'none'}} type='file' accept="image/png, image/jpeg"/>T√©l√©charger votre avatar</Button>
            <Box ml={3} mb={2
            } >
              <RenderAvatar/>
            </Box>
        </Box>
        </DialogContent>    
        <DialogActions>
          <Button onClick={props.close} color="secondary">
            Fermer
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ProfilEdit