import React from 'react'
import {useDispatch} from 'react-redux'
import {Paper,Typography,Box,TextField,Button} from '@material-ui/core/';
import {updateCout,addCout} from '../../Store/modules/budgetStore'
import InputAdornment from '@material-ui/core/InputAdornment';
import Icon from '@material-ui/core/Icon';
import {onlyNumber} from '../../Utils'


const BudgetEdit = (props) => {

    const dispatch = useDispatch()
    const [cout,setCout] = React.useState({nom:"",coutPre:"",coutReel:""})

    React.useEffect(() => {

        setCout(props.cout)
    },[setCout,props.cout])
  
    
    const handleInput = (e) => {

        let {name,value} = e.target

        setCout({...cout,[name]:value})
    }


    const handleInputNumber = (e) =>  {
        
        let {name,value} = e.target

        if(onlyNumber(value)){setCout({...cout,[name]:value})}
        
    }

    const handleCancel = () => {setCout(props.cout);props.close()}

  
    const handleSubmit = () => {

       props.cout.nom !== "" ?  dispatch(updateCout(cout)) : dispatch(addCout(cout))
       props.close()
    }

    const RenderTitre = () => {

        if(props.cout.titre === "Saisir libell√©"){

            return(<Box><TextField helperText={props.cout.titre} onChange={handleInput} size="small"  name="nom" margin="dense" label="Libell√©" value={cout.nom}/></Box>)

        } else { return(<Typography variant="h6" color="secondary" >{cout.nom}</Typography>)}
    }

    return(

        <Paper elevation={2}>
            <Box p={2} display="flex" flexDirection="column">
                {RenderTitre()}
                <Box mt={1}>
                    <TextField onChange={handleInputNumber} 
                    size="small" name="coutPre" margin="dense" label="Cout pr√©visionnel" 
                    InputProps={{ endAdornment : ( <InputAdornment position="end"><Icon size="small">euro</Icon></InputAdornment>)}}
                    value={cout.coutPre}/>
                </Box>
                <Box mt={1}>
                    <TextField 
                    onChange={handleInputNumber} 
                    size="small"  name="coutReel"  margin="dense" label="Cout r√©el" 
                    InputProps={{ endAdornment : ( <InputAdornment position="end"><Icon size="small">euro</Icon></InputAdornment>)}}
                    value={cout.coutReel}/>
                </Box>
            </Box>
            <Box p={1} display="flex" justifyContent="space-around">
                <Button onClick={()=>handleSubmit()} size="small" color="primary">Validez</Button>
                <Button onClick={handleCancel} size="small" color="primary">Annuler</Button>
            </Box>
        </Paper>
    )
}


export default BudgetEdit