import React,{useState,useEffect} from 'react'
import {useSelector} from 'react-redux'
import {loadingSelector} from '../Store/modules/authStore'
import {  Grid, Typography } from '@material-ui/core'


const Home = () => {

    const loading = useSelector(loadingSelector)
    const [displayStyle,setDisplayStyle] = useState("none")

    useEffect(() => {

        const timer = setTimeout(() => { setDisplayStyle("")
            
        }, 1000);

        return()=> clearTimeout(timer)
    },[]);

 

    return( 

        <Grid container display="flex" justify="center" style={{display:displayStyle}} >
            {loading &&   <Grid item md={6} className='textColor'>
                <Typography color='primary' variant='h5'>Bienvenue sur Wedd Easy !</Typography>
                <Typography >L'appli qui vous accompagne dans l'organisation de votre √©venement !</Typography>
            </Grid>}
        </Grid>
    )
}

export default Home