import React,{useState} from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import {Box,Typography,IconButton} from '@material-ui/core/';
import Icon from '@material-ui/core/Icon';

const FormInvit = (props) => {
  
  const input = {nom:"",prenom:"",eventId:props.eventId,uid:props.uid,id: Date.now()}
  const [host,setHost] = useState(input)

  const handleInput = (event) => {

    const {value,name} = event.target

    setHost({...host,[name]:value})
  }

  const handleSubmit = () => { props.field.prenom ? props.addInvite(host) : props.addTable(host.nom);props.Close()}

  const handleClose = () => { props.Close()};

  return (
    <div >
      <Dialog  open={true} onClose={handleClose} aria-labelledby="form-dialog-title">
      <Box mt={2} ml={3}>
        <Typography color='secondary' variant="h5">{props.field.titre}</Typography>
      </Box>
        <DialogContent>
          <TextField
            onChange={handleInput}
            margin="dense"
            name={props.field.nom}
            label={props.field.nom}
            type="text"
            fullWidth
            color="primary"
            value={host.nom}
          />{props.field.prenom && 
           <TextField
            onChange={handleInput}
            margin="dense"
            name={props.field.prenom}
            label={props.field.prenom}
            type="text"
            fullWidth
            color="primary"
            value={host.prenom}
          />}
        </DialogContent>
        <DialogActions>
            <Box display="flex" justifyContent='space-between' style={{width:"100%"}}>
                <Box>
                   <IconButton onClick={()=> props.Close()}><Icon color="primary" >close_box</Icon></IconButton>
                </Box>
                <Box>
                    <Button onClick={handleClose} color="primary">
                        Annuler
                    </Button>
                    <Button onClick={handleSubmit} color="primary">
                        Enregister
                    </Button>
                </Box>
          </Box>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default FormInvit