import React,{useState} from 'react'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import {Box,Typography,IconButton} from '@material-ui/core/';
import Icon from '@material-ui/core/Icon';

const formPresta = (props) => {         

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [presta,setPresta] = useState(props.edit)

    const handleClose = () => props.Close()

    const handleInput = (event) => {

        const {value,name} = event.target
        setPresta({...presta,[name]:value})
    }
    
    const handleSubmit = () => { 

        let prestObj = {...presta,categorie:props.field}
        props.handleSubmit(prestObj)
        props.Close()
    }

    const handleClearField = () => {

        setPresta(props.edit)
        props.handleSetEdit(props.edit)
    }

    return(
        <Dialog open={true} onClose={handleClose} aria-labelledby="form-dialog-title" >
            <Box mt={2} ml={3}>
                <Typography color="secondary" variant="h5" >{props.field}</Typography>
            </Box>
            <DialogContent>
                <TextField
                 onChange={handleInput}
                 margin="dense"
                 name="nom"
                 label="Nom du pr√©stataire"
                 type="text"
                 fullWidth
                 color="primary"
                 value={presta.nom}
                >
                </TextField>
                <TextField
                 onChange={handleInput}
                 margin="dense"
                 name="adresse"
                 label="Adresse du pr√©stataire"
                 type="text"
                 fullWidth
                 color="primary"
                 value={presta.adresse}
                >
                </TextField>
                <TextField
                 onChange={handleInput}
                 margin="dense"
                 name="phone"
                 label="T√©l√©phone du pr√©stataire"
                 type="text"
                 fullWidth
                 color="primary"
                 value={presta.phone}
                >
                </TextField>
                <TextField
                 onChange={handleInput}
                 margin="dense"
                 name="mail"
                 label="Adresse mail"
                 type="text"
                 fullWidth
                 color="primary"
                 value={presta.mail}
                >
                </TextField>
                <TextField
                 onChange={handleInput}
                 margin="dense"
                 name="web"
                 label="Site web"
                 type="text"
                 fullWidth
                 color="primary"
                 value={presta.web}
                >
                </TextField>
            </DialogContent>
            <DialogActions>
            <Box display="flex" justifyContent='space-between' style={{width:"100%"}}>
                <Box>
                   <IconButton onClick={()=> props.Close()}><Icon color="primary" >close_box</Icon></IconButton>
                </Box>
                <Box>
                    <Button onClick={()=>handleClearField()} color="primary">
                        Annuler
                    </Button>
                    <Button onClick={handleSubmit} color="primary">
                        Enregister
                    </Button>
                </Box>
            </Box>
            </DialogActions>
        </Dialog>
    )
}


export default formPresta