import React,{useState,useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import {currentEventSelector} from '../../Store/modules/evntStore'
import {AddPresta,fetchPresta,prestasSelector,deletePresta,updatePresta} from '../../Store/modules/prestaStore'
import {Grid, Typography,Box, Container,IconButton} from '@material-ui/core/'
import {useStyles} from '../../ConfigMaterial/index'
import Icon from '@material-ui/core/Icon';
import FormPresta from '../../Components/Dashboard/FormPresta'
import CardPresta from '../../Components/Dashboard/CardPresta'

const PrestatairesId = () => {

    const [open,setOpen] = useState(false)
    const [field,setField] = useState()

    const dispatch = useDispatch()

    const event = useSelector(currentEventSelector)

    const initialState ={categorie:field, nom:"",adresse:"",phone:"",mail:"",web:""}

    const [edit,setEdit] = useState(initialState)

    useEffect(() => {

        dispatch(fetchPresta(event.eventId))

    },[dispatch,event.eventId])

    const prestas = useSelector(prestasSelector)

    const fieldName = [
        {nom:"Restaurant, salle, salon", icon:"add_circle_outline"},
        {nom:"Son, vid√©o, musique", icon:"add_circle_outline"},
        {nom:"Traiteur", icon:"add_circle_outline"},
        {nom:"Divers", icon:"add_circle_outline"}
    ]

    const openForm = (field) => {

        setField(field)
        setOpen(true)
    } 

    const handleSetEdit = (presta) =>  setEdit(presta)

    const closeForm = () => {setOpen(false);handleSetEdit(initialState)}

    const handleSubmit = (presta) => { 
        
        edit.nom !== '' ? dispatch(updatePresta(presta,event.eventId)) :  dispatch(AddPresta(presta,event.eventId));
        closeForm()
    }

    const handleDeletePresta = (item) => {dispatch(deletePresta(item))}

    const classes = useStyles()

    return(
        <Container>
            <Box mb={2}>
                <Typography variant="h4" color="secondary">Prestatires</Typography>
                <div style={{width:"17%"}} className={classes.underLine}></div>
            </Box>
            {open && <FormPresta handleSetEdit={handleSetEdit} Close={closeForm} edit={edit} field={field} handleSubmit={handleSubmit}/>}
            <Box display="flex" flexWrap="wrap">
            {fieldName.map((item,index) => 
                <Grid item md={3} key={index} >
                   <Box mt={3} display="flex" alignItems="center">
                        <IconButton onClick={() => openForm(item.nom)} color="primary" size="small"><Icon>{item.icon}</Icon></IconButton>
                        <Box ml={1}>
                            <Typography color="primary" vairant='h4'>{item.nom}</Typography> 
                        </Box>
                   </Box>
                   <div style={{width:"80%"}} className={classes.underLine}></div>
                   <Box mt={1}>
                      {prestas.map((presta,index) => presta.categorie === item.nom ?  <CardPresta 
                       key={index} handleDelete={handleDeletePresta} handleEdit={handleSetEdit} CloseForm={closeForm} openForm={openForm} item={presta} index={index}/> : null)} 
                   </Box>
               </Grid> 
            )}
            </Box>
        </Container>
    )
}

export default PrestatairesId