import React from 'react'
import {  Link,useRouteMatch} from 'react-router-dom'
import {Box,Chip} from '@material-ui/core/';

const DashNavMenu = () => {

    let match = useRouteMatch()
  
    return(
        <Box className='menuDashBg' style={{height:"89.1vh"}}>
            <Box>
               <Box pt={7} ml={2} >
                   <Link className="text-link" to={`${match.url}/EventId`}><Chip
                        label="Evenement"
                        clickable
                        color="secondary"
                        style={{width:'105px'}}
                    /></Link>
                </Box>
                <Box mt={5} ml={2}>
                <Link className="text-link" to={`${match.url}/PrestatairesId/`}><Chip
                        label="Prestataires"
                        clickable
                        color="secondary"
                        style={{width:'105px'}}
                    /></Link>
                </Box>
                <Box mt={5} ml={2}>
                <Link className="text-link" to={`${match.url}/PlanId/`}><Chip
                        label="Plan de table"
                        clickable
                        color="secondary"
                        style={{width:'105px'}}
                    /></Link>
                </Box>
                <Box mt={5} ml={2}>
                    <Link className="text-link" to={`${match.url}/InvitId/`}><Chip
                        label="Invitation"
                        clickable
                        color="secondary"
                        style={{width:'105px'}}
                    /></Link>
                </Box> 
                <Box mt={5} ml={2}>
                <Link className="text-link" to={`${match.url}/BudgetId`}><Chip
                        label="Budget"
                        clickable
                        color="secondary"
                        style={{width:'80px'}}
                    /></Link>
                </Box>
            </Box>
        </Box>
    )
}

export default DashNavMenu