import {createSlice} from '@reduxjs/toolkit'
import * as firebase from '../../Firebase/fbase' 


const initialState = { hosts : [], tables : []}

export const slice = createSlice({

    name:"Hosts",

    initialState: initialState,

    reducers : {

        setHosts: (state,{payload}) => {state.hosts = payload},

        setDeleteHost:(state,{payload}) => {state.hosts = state.hosts.filter(item => item.id !== payload.id)},

        setTables:(state,{payload}) => {state.tables = payload},

        setAddTable:(state,{payload}) => {state.tables = [...state.tables,payload]},

        setDeleteTable:(state,{payload}) => {state.tables = state.tables.filter(tab => tab.id !== payload.id)}
    }
})

export const addHosts = (payload) => async dispatch => {

    try {

        const host = await firebase.db.collection('hosts').add({nom:payload.nom,prenom:payload.prenom,eventId : payload.eventId,uid:payload.uid,id:payload.id})
        
        await firebase.db.collection('hosts').doc(host.id).update({hostId:host.id})

        dispatch(fetchHosts(payload.eventId))

    } catch (error) {console.log(error)}
}

export const deleteHost = (payload) => async dispatch => {

    try {

        let selectedHost;

        const host = await firebase.db.collection('hosts').where("id", "==", payload.id).get()

        host.forEach(item => selectedHost = item.data())
        
        await firebase.db.collection('hosts').doc(selectedHost.hostId).delete()

        dispatch(setDeleteHost(payload))
        dispatch(fetchHosts(payload.eventId))
        
    } catch (error) {console.log(error)}
}

export const fetchHosts = payload => async dispatch => {

    try {

        const hosts = await firebase.db.collection('hosts').where("eventId", "==", payload).get()

        if(hosts.empty){

            console.log("no hosts")

        } else {

            const hostsArray = []

            hosts.forEach(host => {

                let data = host.data()
                    
                hostsArray.push(data)

                hostsArray.sort((a,b)=> {return  a.nom > b.nom ? 1 : -1})
            })
            
            dispatch(setHosts(hostsArray))
        }

        dispatch(fetchTables(payload))

    } catch (error) {console.log(error)}
}

export const sortHosts = (payload,ord) => dispatch => {

    const sorted = [...payload]

    !ord ? sorted.sort((a,b) => { return a.nom > b.nom ? -1 : 1}) : sorted.sort((a,b) => { return a.nom > b.nom ? 1 : -1})

   dispatch(setHosts(sorted))
}

export const createPlanTables = (payload) => async dispatch => {

    try {

        const table = await firebase.db.collection('tables').add({arrayTable: payload.arrayTable, eventId:payload.eventId,nom:payload.nom,id:payload.id})
        
        await firebase.db.collection('tables').doc(table.id).update({tableId:table.id})
        
        dispatch(fetchTables(payload.eventId))
    } catch (error) {console.log(error) }
}

export const fetchTables = (payload) => async dispatch => {

    try {

        const tables = await firebase.db.collection('tables').where("eventId", "==", payload).get()

        if(tables.empty){ 

            console.log("no tables")

        } else {

            let tabArr = []

            tables.forEach(tab => { tabArr.push(tab.data())})

           dispatch(setTables(tabArr))
        }
        
    } catch (error) {console.log(error)}
}

export const deleteTable = (payload) => async dispatch => {

    try {

        let tableSelected;

        const tables = await firebase.db.collection('tables').where("id", "==", payload).get()

        tables.forEach(tab => { tableSelected = tab.data()})

        await firebase.db.collection("tables").doc(tableSelected.tableId).delete()

        dispatch(fetchTables(tableSelected.eventId))
        dispatch(setDeleteTable(tableSelected))
        
    } catch (error) {console.log(error)}

    
}

export const updateTables = (payload) => async dispatch => {

    let tableSelected;

    try {

        const tables = await firebase.db.collection('tables').where("id","==",payload.id).get()

        tables.forEach( async tab => {

            tableSelected = tab.data()

           await firebase.db.collection('tables').doc(tableSelected.tableId).update({arrayTable:payload.arrayTable})
        })

        dispatch(fetchTables(payload.eventId))
        
    } catch (error) {console.log(error)}
}


export const {setHosts,setDeleteHost,setTables,setAddTable,setDeleteTable} = slice.actions

export default slice.reducer

export const hostsSelector = (state) => state.hostStore.hosts
export const tablesSelector = (state) => state.hostStore.tables

